.loader {
    border: 16px solid #f3f3f3;
    border-top: 16px solid #8CC153;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.loaderWrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loaderHeader {
    font-style: normal;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 40px !important;
    line-height: 120%;
    color: #8CC153;
}

@media (max-width: 768px) {
    .header {
        font-size: 24px !important;
    }
}